@media (min-width: 320px) {
    .help {
        background-color: #4da1ff;
        &-title {
            color: #ffffff;
            font-size: 20px;
            font-weight: 900;
            line-height: 30px;
            padding-top: 55px; }
        &-desc {
            color: #ffffff;
            font-size: 10px;
            font-weight: 300;
            line-height: 10px;
            padding-top: 21px; }
        &-button {
            padding-top: 34px;
            padding-bottom: 144px;
            button {
                color: #ffffff;
                font-size: 10px;
                font-weight: 900;
                border-radius: 6px;
                border: 2px solid #ffffff;
                background-color: transparent;
                line-height: 35px;
                text-align: center;
                width: 320px;
                height: 35px; } } } }
