@media (min-width: 320px) {
    .header {
        display: flex;
        &-logo {
            margin-top: 40px;
            &_big {
                font-size: 13px;
                font-weight: 900; }
            &_small {
                font-size: 7.4px; } }
        &-contact {
            margin-top: 45px;
            &__tel {
                font-size: 10px; }
            &__city {
                font-size: 6.4px;
                width: 89px; } }
        &-menu {
            order: 4;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: -87px;
            margin-left: -6px;
            z-index: 4;
            padding-right: 0;
            .ul {
                display: flex;
                transition: 0.5s;
                transform-origin: right center;
                transform: translateX(20px);
                opacity: 0;
                background-color: #949ba4;
                width: 390px;
                padding: 0;
                padding-right: 25px;
                height: 37px;
                line-height: 37px;
                border-top-left-radius: 35px;
                border-bottom-left-radius: 35px;
                .link {
                    color: #f8fafc;
                    &:hover {
                        color: #313c47; }
                    &-active {
                        color: #313c47; } }
                &_active {
                    opacity: 1;
                    transform: scaleX(1) translateX(20px); } } } }
    .link {
        font-size: 10px; }
    .burger {
        order: 2;
        width: 50px;
        height: 37px;
        background-color: #949ba4;
        border-radius: 50%;
        margin-top: 35px;
        z-index: 3;
        position: relative;
        cursor: pointer;
        span {
            position: absolute;
            top: 50%;
            margin-top: -1px;
            left: 50%;
            margin-left: -10px;
            height: 2px;
            width: 20px;
            background-color: #313c47;
            &:before, &:after {
                position: absolute;
                top: 50%;
                margin-top: -1px;
                left: 50%;
                margin-left: -10px;
                height: 2px;
                width: 20px;
                display: block;
                transition: 0.5s;
                content: '';
                background-color: #313c47; }
            &:before {
                transform: translateY(-5px); }
            &:after {
                transform: translateY(5px); } }
        &_active {
            span {
                height: 0;
                &:before {
                    transform: rotate(45deg); }
                &:after {
                    transform: rotate(-45deg); } } } } }
