@media (min-width: 320px) {
    .filter {
        margin-top: 80px;
        &-katalog {
            &__home {
                color: #949ba4;
                font-family: Ionicons;
                font-size: 16px;
                font-weight: 500;
                vertical-align: middle; }
            &__arrow {
                fill: rgba(#949ba4, 0.24);
                width: 8px;
                height: 13px;
                vertical-align: middle; }
            &__name {
                color: #b4b9bf;
                font-size: 16px;
                font-weight: 500;
                vertical-align: middle; } }
        &-text {
            margin-top: 28px;
            &__title {
                color: #2e3943;
                font-size: 36px;
                font-weight: 500; }
            &__desc {
                color: #949ba4;
                font-size: 16px;
                font-weight: 300;
                line-height: 24px;
                margin-bottom: 45px; } }
        &-search {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            margin-top: 30px;
            &__close {
                width: 40px;
                height: 53px;
                fill: #dfe5eb; }
            &__status {
                width: 250px;
                height: 64px;
                border-radius: 6px;
                border: 1px solid #eaf0f5;
                background: url('../img/select.png') center;
                background-color: #f8fafc;
                padding-left: 30px;
                line-height: 64px;
                color: #949ba4;
                font-size: 18px;
                font-weight: 300;
                appearance: none; }
            &__INN {
                width: 250px;
                height: 64px;
                border-radius: 6px;
                border: 1px solid #eaf0f5;
                background-color: #f8fafc;
                padding-left: 30px;
                line-height: 64px;
                font-size: 18px;
                font-weight: 300;
                margin-top: 10px; }
            &__date {
                width: 250px;
                height: 64px;
                border-radius: 6px;
                border: 1px solid #eaf0f5;
                background-color: #f8fafc;
                padding-left: 30px;
                line-height: 64px;
                font-size: 18px;
                font-weight: 300;
                margin-top: 10px; }
            &__button {
                width: 250px;
                height: 64px;
                border-radius: 6px;
                border: 1px solid #4da1ff;
                background-color: #4da1ff;
                text-align: center;
                line-height: 64px;
                font-size: 16px;
                font-weight: 900;
                color: #ffffff;
                margin-top: 10px;
                svg {
                    width: 14px;
                    height: 12px; } } }
        &-header {
            border-radius: 6px 6px 0 0;
            background-color: #f3f6f9;
            font-size: 9px;
            font-weight: 700;
            color: #929aa8;
            height: 66px;
            line-height: 66px; }
        &-traffic {
            background-color: #f8fafc;
            margin-top: 105px;
            padding-top: 100px;
            &__table {
                width: 345px;
                margin: 0 auto;
                color: #929aa8;
                td {
                    padding-top: 14px;
                    padding-bottom: 14px;
                    background: #ffffff;
                    font-size: 8px; }
                .status {
                    padding-left: 15px;
                    padding-right: 10px;
                    img {
                        width: 20px;
                        height: 20px; } }
                tr {
                    border: 1px solid #f4f7f9; }
                .name {
                    color: #687790; } } } } }
